.buttonWrapper {
	width: 50%;
	max-width: 200px;
	min-width: 100px;
}
.boardTitleBox {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 50%;
	max-width: 200px;
	min-width: 100px;
	margin-right: 10px;
	font-weight: 500;
	font-size: 1.2rem;
	line-height: 1.3;
	letter-spacing: 0.02857em;
	padding: 6px 16px;
	border-radius: 4px;
	color: #fff;
	background-color: #2e7d32;
	box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
		0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}
